@use "." as this;
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Display";

#{this.$selector} {
    #{Input.$selector} {
        @include Margin.set($top: alpha, $bottom: none);
    }

    .hiddenInput {
        @include Display.set($display: none);
    }
}
