@use "." as this;
@use "../InputCredential";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";

#{this.$selector} {
    #{InputCredential.$selector} {
        @include Margin.set($top: alpha, $bottom: none);
    }

    .footer {
        width: Layout.get-max-width(alpha);
        #{Button.$selector}:nth-child(2) {
            @include Margin.set($top: beta, $bottom: beta);
        }
    }
}
